import React, {useState} from 'react'


export default function TextForm(props) {
    const HandleUppercaseClick =()=>{
        let newtext=texting.toUpperCase();
        setText(newtext);
        props.showAlert("Converted to upper case","success")
    }
    const HandleLowcaseClick=()=>{
        let newtext=texting.toLowerCase();
        setText(newtext);
        props.showAlert("Converted to lower case","success")
    }
    
    const OnchangeHandle=(event)=>{
        setText(event.target.value)
    }
    const [texting, setText] = useState('');
    
    return (
       <> 
       
        <div className="container" style={props.style}>
            <h1>{props.heading}</h1>
        <label htmlFor="myText" className="form-label"></label> 
        <textarea value={texting} style={props.style}  onChange={OnchangeHandle} className="form-control" name="" id="myText" cols="30" rows="10" placeholder="Start writing"></textarea>
        <button disabled={texting.length===0} className="btn btn-primary my-2 mx-2" onClick={HandleUppercaseClick}>Convert to Uppercase</button>
        <button disabled={texting.length===0}  className="btn btn-primary my-2 mx-2" onClick={HandleLowcaseClick}>Convert to Lowercase</button>
        
        </div>
        <div className="container" style={props.style}>
            <h3>Your text summary</h3>
            <p> {texting.split(" ").filter((element)=>{return element.length !==0}).length} words and {texting.length} characters</p>
            <h3>Preview</h3>
            <p>{texting.length>0?texting:"Nothing to preview"}</p>
        </div>    
      </>
    )
}
