// import { a } from 'react-router-dom'
import React from 'react'
import PropTypes from 'prop-types'


export default function Navbar(props) {
    return (
        <>
        <div >
        <nav style={props.style} className="navbar navbar-expand-lg">
       
  <div className="container-fluid">
    <a className="navbar-brand" href="#">My Application</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div  style={props.style} className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item" style={props.style}>
          <a className="nav-a active mx-2" aria-current="page" href="">Home</a>
        </li>
        <li className="nav-item" style={props.style}>
          <a className="nav-a" href="#">About</a>
        </li>
        <li className="nav-item dropdown" style={props.style}>
          <a className="nav-a dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            {props.more}
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown" style={props.style}>
            <li><a className="dropdown-item" href="#" style={props.style}>Contact</a></li>
            <li><a className="dropdown-item" href="#" style={props.style}>Services</a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" href="#" style={props.style}>{props.dpdown}</a></li>
          </ul>
        </li>
        
      </ul>
      <form className="d-flex">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form>
    </div>
  </div>
</nav>
        </div>
        </>
    )
}
Navbar.propTypes=
{title: PropTypes.string,
dpdown:PropTypes.string}

Navbar.defaultProps={
  title:"Set title",
  dpdown:"More about me"
}