import './App.css';
import React, {useState} from 'react'
import About from './Components/About';
import Navbar from './Components/Navbar';
import TextForm from './Components/TextForm';
import Alert from './Components/Alert';
// import {
//   BrowserRouter as Router,
//   Switch,
//   Route
// } from "react-router-dom";
 
function App() {

  const  [alert, setalert] = useState(null);
  const showAlert=(message,type)=>{
       setalert({
         msg:message,
         type:type
       })
       setTimeout(() => {
         setalert(null)
       }, 1500);
  }
  
  const [mstyle, setmstyle] = useState({
    color:"black",
    backgroundColor:"white",
    
  })
  const toggleStyle=()=>{
    if(mstyle.color==='white'){
      setmstyle({
        color:"black",
        backgroundColor:"white" ,
      })
      showAlert("Light mode is enabled","success");
      document.title="TextUtility-LightMode"
      document.body.style.backgroundColor="white"
    }
    else {
      setmstyle({
        color:"white",
        backgroundColor:"black"
      })
      showAlert("Dark mode is enabled","success");
      document.title="TextUtility-DarkMode"
      document.body.style.backgroundColor="black"
    }
  }
  
  return (
    <>  
    {/* <Router> */}
     <Navbar style={mstyle}/>
     <Alert alert={alert}/>
     <div className="Toggle">
            <div className="form-check form-switch">
           <input onClick={toggleStyle}  className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" />
           <label className="form-check-label" for="flexSwitchCheckDefault" style={mstyle} >Dark Mode</label>
           </div>
        </div>
     <div className="container">
     {/* <Switch>
          <Route exact path="/about">
            <About exact style={mstyle}/>
          </Route>      
          <Route path="/">
          <TextForm heading="Write something cool" style={mstyle} showAlert={showAlert} /> 
          </Route>
    </Switch> */}
     <TextForm heading="Write something cool" style={mstyle} showAlert={showAlert} /> 
     <About style={mstyle}/>
     </div>
     {/* </Router> */}
    </> 
  );
}

export default App;