import React from 'react'

export default function About(props) {
    return (
      <>
        <div className="conatiner" style={props.style}>
            <h1>About us</h1>
            <div className="accordion" id="accordionExample">
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingOne">
      <button style={props.style} className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
        Founder 1
      </button>
    </h2>
    <div id="collapseOne" className="accordion-collapse collapse " aria-labelledby="headingOne" data-bs-parent="#accordionExample">
      <div className="accordion-body" style={props.style}>
      <strong> Dev Ashish </strong>
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingTwo">
      <button style={props.style} className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
        Founder 2
      </button>
    </h2>
    <div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
      <div className="accordion-body" style={props.style}>
      <strong> Dev Ashish </strong>
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingThree">
      <button style={props.style} className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
        Founder 3
      </button>
    </h2>
    <div id="collapseThree" className="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
      <div className="accordion-body" style={props.style}>
      <strong> Dev Ashish </strong>
      </div>
    </div>
  </div>
</div>
         </div>
        {/* {<div className="container">
<button onClick={toggleStyle} type="button" class="btn btn-primary my-2">Enable {buttonText} mode</button>
</div> */} 
          {/* <div className="Toggle">
            <div className="form-check form-switch">
           <input onClick={toggleStyle} className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" />
           <label className="form-check-label" for="flexSwitchCheckDefault" >Light Mode</label>
           </div>
        </div> */}
        </>
    )
}


